/* src/layout/topbar/Topbar.module.css */
.topbar-container {
  width: 100%;
  height: 50px;
  background-color: white;
  position: sticky;
  top: 0;
  z-index: 999;
  box-shadow: 0 1px 6px 0 #e7f1ff55;
}
.topbar {
  display: flex;
  height: 100%;
  padding: 0px 20px;
  align-items: center;
  justify-content: space-between;
}
.logo {
  font-weight: bold;
  font-size: 30px;
  color: #2264a9;
  cursor: pointer;
  font-family: 'Quicksand','Noto Sans KR', sans-serif;
  letter-spacing: 1px;
  transition: color 0.16s;
}
.logo:hover { color: #2e93e3; }
.topbar-right {
  display: flex;
  align-items: center;
}
.topbar-icon {
  position: relative;
  margin-right: 15px;
  color: #555;
  cursor: pointer;
  background: none;
  border-radius: 8px;
  padding: 2px;
  transition: background 0.16s, transform 0.16s;
}
.topbar-icon img {
  width: 30px;
  height: 30px;
}
.topbar-icon:hover {
  transform: scale(1.17);
  background-color: #e6f2fc;
}
.topbar-icon:active {
  opacity: 0.8;
}
@media (max-width: 600px) {
  .logo { font-size: 20px; }
  .topbar { padding: 0 4px; }
  .topbar-icon img { width: 24px; height: 24px; }
  .topbar-icon { margin-right: 7px; }
}
